.expertise {
  padding: 148px 20px;
  background-color: #fff;

  &__heading {
    @include section-heading;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    margin-top: 64px;
  }

  &__item {
    flex-basis: 265px;
    text-align: center;
    cursor: pointer;
  }

  &__img {
    max-width: 166px;
    margin-bottom: 40px;
    border-radius: 24px;
  }

  &__title {
    margin-bottom: 16px;

    color: $heading-color-dark;
    line-height: 140%;
  }

  &__text {
    font-size: 0.7rem;
    line-height: 150%;
    color: $text-color-dark;
  }
}
